body {
  opacity: 1;
  background-blend-mode: multiply;
  background-color: #e5e5f7;
  background-image: radial-gradient(circle, #444cf7, #e5e5f7), repeating-radial-gradient(circle, #444cf7, #444cf7, 10px, #0000 20px 10px);
}

#header {
  background-color: #f5f5f5;
  border-radius: 8px;
  padding: 16px;
  box-shadow: 0 4px 8px #0000001a;
}

#header h1 {
  text-align: center;
  margin-bottom: 16px;
  font-size: 28px;
}

form {
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  display: flex;
}

label {
  margin-bottom: 8px;
  font-size: 16px;
  font-weight: bold;
}

input[type="text"] {
  width: 50%;
  box-sizing: border-box;
  background-color: #fff;
  border: 2px solid #ddd;
  border-radius: 4px;
  margin-top: 16px;
  margin-bottom: 8px;
  padding: 12px 16px;
  font-size: 16px;
}

input[type="text"]:focus, textarea:focus {
  border: 2px solid #4caf50;
  outline: none;
}

button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  margin-top: 16px;
  margin-right: 8px;
  padding: 12px 16px;
  font-size: 16px;
}

button[type="submit"]:hover {
  background-color: #45a049;
}

#dele-button {
  cursor: pointer;
  background-color: #d9534f;
  border: none;
  border-radius: 4px;
  margin-top: 10px;
  margin-right: 8px;
  padding: 12px 16px;
}

#dele-button:hover {
  background-color: #c9302c;
}

.hidden {
  display: none;
}

.modal {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: #0006;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  width: 80%;
  background-color: #fefefe;
  border: 1px solid #888;
  margin: 15% auto;
  padding: 20px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover, .close:focus {
  color: #e30c0c;
  cursor: pointer;
  text-decoration: none;
}

h4 {
  cursor: pointer;
  color: #4caf50;
  text-transform: uppercase;
  letter-spacing: 2px;
  background: #000;
  font-size: 24px;
  font-weight: bold;
}

